<template>
  <section class="step-8">
    <div class="add-place-des">
      <h4>Price & Discount</h4>
    </div>
    
    <div class="step8-body">
      <div class="row">
        <div class="col-6">
          <ValidationObserver v-slot="{ invalid }">
            <form @submit.prevent="" class="form">
              <ValidationProvider
                name="pricePerDay"
                rules="required|numeric"
                v-slot="{ errors }"
              >
                <div class="form-input-wrapper">
                  <label for="">Price per day (SDG)</label>
                  <div class="form-input-element">
                    <input v-model="pricePerDay" type="number" />
                  </div>

                  <span class="form-error">{{ errors[0] }}</span>
                </div>
              </ValidationProvider>
              <div class="step8-discount">
                <div class="discount-check">
                  <label class="check-container">
                    <span class="check-txt">
                      I want to make discount for long period stays
                    </span>
                    <input type="checkbox" v-model="hasDiscount" />
                    <span class="checkmark"></span>
                  </label>
                </div>

                <transition name="fade">
                  <div class="discount-values" v-if="hasDiscount">
                    <ValidationProvider
                      name="longPeriodDiscountPeriod"
                      rules="required|numeric"
                      v-slot="{ errors }"
                    >
                      <div class="form-input-wrapper">
                        <label for="">Discount period (days)</label>
                        <div class="form-input-element">
                          <input
                            v-model="longPeriodDiscountPeriod"
                            type="number"
                          />
                        </div>

                        <span class="form-error">{{ errors[0] }}</span>
                      </div>
                    </ValidationProvider>
                    <ValidationProvider
                      name="longPeriodDiscountDiscount"
                      rules="required|numeric"
                      v-slot="{ errors }"
                    >
                      <div class="form-input-wrapper">
                        <label for="">Discount price (SDG)</label>
                        <div class="form-input-element">
                          <input
                            v-model="longPeriodDiscountDiscount"
                            type="number"
                          />
                        </div>

                        <span class="form-error">{{ errors[0] }}</span>
                      </div>
                    </ValidationProvider>
                    <span class="discount-helper">
                      I will discount {{ longPeriodDiscountDiscount }} SDG for
                      people who stay at least for
                      {{ longPeriodDiscountPeriod }} days
                    </span>
                  </div>
                </transition>
              </div>

              <Navigation :valid="!invalid" />
            </form>
          </ValidationObserver>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import Navigation from "./Navigation.vue";
import { ValidationObserver, ValidationProvider, extend } from "vee-validate";
import { numeric, required } from "vee-validate/dist/rules";

extend("required", {
  validate(value) {
    return {
      required: true,
      valid: ["", " ", null, undefined].indexOf(value) === -1,
      message: "This field is required",
    };
  },
  computesRequired: true,
});

extend("numeric", {
  ...numeric,
  message: "This field only accept numbers",
});

export default {
  components: { Navigation, ValidationObserver, ValidationProvider, extend },
  data() {
    return {};
  },
  computed: {
    pricePerDay: {
      get() {
        return this.$store.state.steps.step8.pricePerDay;
      },
      set(value) {
        this.$store.commit("setPricePerDay", { pricePerDay: value });
      },
    },
    hasDiscount: {
      get() {
        return this.$store.state.steps.step8.longPeriodDiscount.hasDiscount;
      },
      set(value) {
        this.$store.commit("setLongPeriodDiscount", { hasDiscount: value });
      },
    },
    longPeriodDiscountPeriod: {
      get() {
        return this.$store.state.steps.step8.longPeriodDiscount
          .longPeriodDiscountPeriod;
      },
      set(value) {
        this.$store.commit("setLongPeriodDiscountPeriod", {
          longPeriodDiscountPeriod: value,
        });
      },
    },
    longPeriodDiscountDiscount: {
      get() {
        return this.$store.state.steps.step8.longPeriodDiscount
          .longPeriodDiscountDiscount;
      },
      set(value) {
        this.$store.commit("setLongPeriodDiscountDiscount", {
          longPeriodDiscountDiscount: value,
        });
      },
    },
  },
};
</script>

<style scoped lang="scss">
/* Customize the label (the container) */
.check-container {
  display: block;
  position: relative;
  padding-left: 1.7rem;
  cursor: pointer;
  font-size: 1.2rem !important;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin-bottom: 0.5rem;
}

/* Hide the browser's default checkbox */
.check-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 5px;
  left: 0;
  height: 20px;
  width: 20px;
  border: 1px solid $text;
  border-radius: 5px;
}

/* On mouse-over, add a grey background color */
.check-container:hover input ~ .checkmark {
  border-color: $textDark;
}

/* When the checkbox is checked, add a blue background */
.check-container input:checked ~ .checkmark {
  background-color: $primary;
  border: none;
  color: $white;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.check-container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.check-container .checkmark:after {
  left: 7px;
  top: 4px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.discount-values {
  margin-top: 1rem;
}
.discount-helper {
  color: $danger;
}
</style>
